
    <template>
      <section class="market-doc-container">
        <h2>Notice 通知</h2>
<h3>基本使用</h3>
<mku-example>
        
        <template slot="source"><element-demo0 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-button @click=&quot;openNotice('open')&quot;&gt;普通通知&lt;/mku-button&gt;
    &lt;mku-button @click=&quot;openNotice('info')&quot;&gt;info类型通知&lt;/mku-button&gt;
    &lt;mku-button @click=&quot;openNotice('error')&quot;&gt;error类型通知&lt;/mku-button&gt;
    &lt;mku-button @click=&quot;openNotice('success')&quot;&gt;success类型通知&lt;/mku-button&gt;
    &lt;mku-button @click=&quot;openNotice('warning')&quot;&gt;warning类型通知&lt;/mku-button&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  methods: {
    openNotice (type) {
      this.$notice[type]({
        title: '提示',
        content: `这是一条 ${type} 类型的通知`
      })
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>描述内容</h3>
<p>描述内容也支持v-html进行动态渲染。</p>
<div class="mku-doc__warning">
<p>动态渲染HTML容易导致XSS攻击。应确保render的值是安全可靠的，禁止将用户提交的内容赋值给该属性。</p>
</div>
<mku-example>
        
        <template slot="source"><element-demo1 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-button @click=&quot;openNotice&quot;&gt;render渲染的描述内容&lt;/mku-button&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  methods: {
    openNotice () {
      const expirationDate = 3
      const couponNums = 2
      this.$notice.warning({
        title: `提示`,
        render: `&lt;p&gt;您还有 &lt;i style=&quot;color:red;&quot;&gt;${couponNums} &lt;/i&gt;张优惠券将在 &lt;i style=&quot;color:red;&quot;&gt;${couponNums} &lt;/i&gt;天后过期，请及时消费。&lt;/p&gt;祝您生活愉快！`
      })
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>自定义显示时间</h3>
<p>默认显示2.5s，支持自定义显示时间，0表示不会自动关闭。</p>
<mku-example>
        
        <template slot="source"><element-demo2 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-button @click=&quot;openNotice1&quot;&gt;自定义2s关闭&lt;/mku-button&gt;
    &lt;mku-button @click=&quot;openNotice2&quot;&gt;不会自定关闭&lt;/mku-button&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  methods: {
    openNotice1 () {
      this.$notice.success({
        title: '提示',
        content: '这是一条2s后关闭的通知',
        duration: 2000
      })
    },
    openNotice2 () {
      this.$notice.success({
        title: '提示',
        content: '这是一条不会自动关闭的通知',
        duration: 0,
        onClose: () =&gt; {
          this.$message.success('关闭成功')
        }
      })
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>事件回调</h3>
<mku-example>
        
        <template slot="source"><element-demo3 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-button @click=&quot;openNotice&quot;&gt;notice&lt;/mku-button&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  methods: {
    openNotice () {
      this.$notice.success({
        title: '提示',
        content: '点击关闭按钮会执行回调函数',
        duration: 0,
        onClose: () =&gt; {
          this.$message.success('关闭成功')
        }
      })
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>Notice Attributes</h3>
