<td style="text-align:center">-</td>
<td style="text-align:center">-</td>
<td style="text-align:center">否</td>
</tr>
<tr>
<td style="text-align:left">onClose</td>
<td style="text-align:left">关闭的回调函数</td>
<td style="text-align:center">Function</td>
<td style="text-align:center">-</td>
<td style="text-align:center">-</td>
<td style="text-align:center">否</td>
</tr>
</tbody>
</table>
